/** @format */

.home_body {
  background-color: #055646;
  height: 100vh;
  width: 100vw;

  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.home_body > p {
  color: white;
  font-size: 30px;
  text-align: center;
  padding: 30px;
}

.home_btn {
  text-decoration: none;
  color: #fff;
  border: #fff 2px solid;

  padding: 10px 30px;
  transition: all 0.35s cubic-bezier(1, 0, 0, 1);
}

.home_btn:hover {
  background-color: #fff;
  border: #055646 2px solid;
  color: #055646;
}
